import Endpoint from '@/services/Endpoint'

export default {
  parties (payload) {
    return Endpoint().get('/s1/nucleus/party', {
      params: payload
    })
  },
  createParty (payload) {
    return Endpoint().post('/s1/nucleus/party', payload)
  },
  modifyParty (payload) {
    return Endpoint().patch('/s1/nucleus/party', payload)
  },
  partyContactMechs (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/contactMech`, {
      params: payload
    })
  },
  createPartyContactMech (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/contactMech`, payload)
  },
  expirePartyContactMech (payload) {
    return Endpoint().delete(`/s1/nucleus/party/${payload.partyId}/contactMech`, {
      params: payload
    })
  },
  partyIdentifications (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/identification`, {
      params: payload
    })
  },
  createPartyIdentification (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/identification`, payload)
  },
  deletePartyIdentification (payload) {
    return Endpoint().delete(`/s1/nucleus/party/${payload.partyId}/identification`, {
      params: payload
    })
  },
  partyContents (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/content`, {
      params: payload
    })
  },
  createPartyContent (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/content`, payload.formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  partyRoles (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/role`, {
      params: payload
    })
  },
  createPartyRole (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/role`, payload)
  },
  deletePartyRole (payload) {
    return Endpoint().delete(`/s1/nucleus/party/${payload.partyId}/role`, {
      params: payload
    })
  },
  partyClassifications (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/classification`, {
      params: payload
    })
  },
  createPartyClassification (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/classification`, payload)
  },
  deletePartyClassification (payload) {
    return Endpoint().delete(`/s1/nucleus/party/${payload.partyId}/classification`, {
      params: payload
    })
  },
  partyBadges (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/badge`, {
      params: payload
    })
  },
  createPartyBadge (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/badge`, payload)
  },
  expirePartyBadge (payload) {
    return Endpoint().delete(`/s1/nucleus/party/${payload.partyId}/badge`, {
      params: payload
    })
  },
  partyNotes (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/note`, {
      params: payload
    })
  },
  createPartyNote (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/note`, payload)
  },
  deletePartyNote (payload) {
    return Endpoint().delete(`/s1/nucleus/party/${payload.partyId}/note`, {
      params: payload
    })
  },
  partyRelations (payload) {
    return Endpoint().get(`/s1/nucleus/party/${payload.partyId}/relationship`, {
      params: payload
    })
  },
  createPartyRelation (payload) {
    return Endpoint().post(`/s1/nucleus/party/${payload.partyId}/relationship`, payload)
  },
  expirePartyRelation (payload) {
    return Endpoint().delete(`/s1/nucleus/party/${payload.partyId}/relationship`, {
      params: payload
    })
  },
  customer (payload) {
    return Endpoint().get('/s1/nucleus/party/customer', {
      params: payload
    })
  }
}
