import PartyService from '@/services/nucleus/PartyService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let PartyTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultPartyTableFilter: {
      class: String,
      default: null
    },
    defaultPartyTableOrderBy: {
      type: String,
      default: null
    },
    roleTypeId: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      partyData: [],
      partyTableData: [],
      partyTableFilterTimer: null,
      partyTableFilter: this.defaultPartyTableFilter,
      partyTableOrderBy: this.defaultPartyTableOrderBy,
      partyTableContainer: 'loading-container',
      partyTableBusy: false,
      partyTableTotalRows: 0,
      partyTableCurrentPage: 1,
      partyTablePerPage: 10
    }
  },
  computed: {
    partyTableNoDataText: function () {
      return this.partyTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    partyTableTotalPages: function () {
      return Math.ceil(this.partyTableTotalRows / this.partyTablePerPage)
    },
    partyTablePagingInfo: function () {
      return this.pagingInfo(this.partyTableCurrentPage, this.partyTablePerPage, this.partyTableTotalRows)
    }
  },
  methods: {
    loadPartyTableData (loadingContainerId) {
      let self = this
      this.partyTableBusy = true
      this.partyTableContainer = loadingContainerId || this.partyTableContainer
      this.startLoading(this.partyTableContainer)
      PartyService.parties({
        pageIndex: this.partyTableCurrentPage - 1,
        pageSize: this.partyTablePerPage,
        filter: this.partyTableFilter,
        orderByField: this.partyTableOrderBy,
        roleTypeId: this.roleTypeId,
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.partyTableContainer)
        self.partyTableBusy = false
        self.partyTableTotalRows = response.data.totalRows
        self.partyTableData = response.data.partyList || []
      }).catch(function (error) {
        self.stopLoading(self.partyTableContainer)
        self.partyTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshPartyTable () {
      this.loadPartyTableData()
    },
    filterPartyTable (filter) {
      let self = this
      if (this.partyTableFilter !== filter) {
        clearTimeout(this.partyTableFilterTimer)
        this.partyTableFilterTimer = setTimeout(() => {
          self.partyTableFilter = filter
          self.loadPartyTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.partyTableFilterTimer)
      }
    },
    orderPartyTable (orderBy, active) {
      this.partyTableOrderBy = (active ? '' : '-') + orderBy
      this.loadPartyTableData()
    }
  },
  watch: {
    partyTableCurrentPage: function () {
      this.refreshPartyTable()
    },
    partyTablePerPage: function () {
      this.refreshPartyTable()
    }
  }
}

export default PartyTableMixin
